@use "../utils" as *;


// 30 march
.accordion {
	&.custom {
		.accordion-item {
			background: var(--color-blue-light2);
			border-color: var(--color-border-light);

			&:first-of-type {
				border-top-left-radius: 8px;
				border-top-right-radius: 8px;
			}

			&:last-of-type {
				border-bottom-right-radius: 8px;
				border-bottom-left-radius: 8px;
			}

			&.accordion-blue {
				border: 0;
				border-radius: 0;
				margin-bottom: 20px;
				background-color: white;

				&>.accordion-header .accordion-button {
					background: #F2F7FC;
					border: 0;
					border-radius: 0;
					padding: 5px 28px;

					img {
						margin-right: 16px;
						width: 50px;
						height: 50px;
						object-fit: contain;
						object-position: center;
					}

					&:after {
						content: none;
					}
				}
			}

			&.accordion-white {
				border: 0;
				border-radius: 0;
				margin-bottom: 16px;
				background-color: var(--color-white);
				box-shadow: 0px 20px 30px 0px #00000014;

				&>.accordion-header .accordion-button {
					background: var(--color-white);
					border: 0;
					border-radius: 0;
					padding: 12px 16px;


					img {
						margin-right: 16px;
						width: 24px;
						object-fit: contain;
						object-position: center;
					}

					.accordion-title {
						h4 {
							@include toRem(font-size, 16);
							@include toRem(line-height, 24);
							font-weight: 400;
						}

						p {
							@include toRem(font-size, 14);
							@include toRem(line-height, 22);
							font-weight: 400;
							color: var(--color-light-gray);
						}
					}
				}
			}
		}

		.accordion-button {
			// display: flex;
			// justify-content: space-between;
			box-shadow: none;
			outline: none;
			border: none;
			background: var(--color-white);
			padding-right: 60px;

			&:not(.collapsed) {
				background: transparent;

				&:after {
					transform: translateY(-50%) rotate(-180deg);
				}
			}

			.button-right {
				display: flex;
				align-items: center;

				.checkbox-inline {
					margin-bottom: 0;
				}
			}

			.title {
				flex-grow: 1;
				color: var(--color-black);
			}

			.btn-delete {
				img {
					height: 15px;
					width: 12.5px;
				}
			}

			&:after {
				width: 10px;
				height: 6px;
				background-image: url("../../images/down-arrow.svg");
				background-size: 10px;
				position: absolute;
				right: 20px;
				top: 50%;
				transform: translateY(-50%)
			}

		}
	}
}